.App {
  background-color: rgb(209, 209, 209);

  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;

  font-size: 22px;
  overflow: auto;
}

.header{
  display: inline-flex;
  justify-content: space-between;
  align-items: center;

  background-color: white;

  position: fixed;
  top: 0;
  left: 0;
  
  width: 100%;
  height: 25%;

  box-shadow: 0 -10px 15px 0px black;
}

.about-me{
  display: inline-flex;
  justify-content: space-around;
  align-items: center;

  width: 50%;
}
.profile-img{
  display: flex;
  padding: 0.1rem 0.1rem;
  margin-left: 2rem;
  border-radius: 50%;
  border: 3px solid black;
  align-items: center;
}

.profile-img img{
  height: 8rem;
  width: 8rem;
  border-radius: 50%;
  border: 1px solid white;
}

.intro{
  margin: 0.5rem;
  flex-shrink: 50;
  text-align: left;
}

.intro p{
  color: rgb(73, 71, 71);
}
.intro h2{
  color: black;
}


.contact{
  margin-right: 4rem;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
  align-items: center;
  align-self: center;
}

#linkedin {
  height: 3.2rem;
}
#mail {
  height: 3rem;
}

.content {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  background-color: rgb(209, 209, 209);;
}

.heading{
  background-color: white;
  align-self: center;
  border-radius: 15px;
  padding: 0.7rem 2.5rem;
  box-shadow: 0 0 10px 0px rgb(156, 156, 156);

  -webkit-background-clip: padding-box; 
  -moz-background-clip:    padding; 
  background-clip:         padding-box;
}

.heading h1{
  margin: 0 0;
  font-size: 1.5rem;
}

.project-list{
  /* padding-top: 25%; */
  position: relative;
  display: inline-flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.project-container{
  background-color: rgb(85, 83, 83);

  width: 14rem;
  height: 11rem;

  margin: 3rem;
  padding: 1rem;

  border-radius: 20px;
}

.project-container:hover{
  box-shadow: 0 0 20px 0px white;
}

.project-container h4{
  font-size: 1.2rem;
  color: white;
}
.description{
  font-size: 1rem;
  color: white;
}

a{
  color: black;
  text-decoration: none;
}